import React, { useState } from "react"
import { FaClipboard, FaHome, FaSun, FaMoon } from "react-icons/fa";

const Header = () => {
  const [dark, setDark] = useState(true);
  const themeHandler = () => setDark(!dark);
  return (
    <header className="mx-auto w-full md:max-w-screen-xl px-16">
      <div className="flex pt-10 items-center justify-between">
        <FaHome className="mx-3 text-gray-600 hover:text-gray-200 cursor-pointer" />
        <div className="flex">
          <FaClipboard className="mx-3 text-gray-600 hover:text-gray-200 cursor-pointer" />
          {dark ? <FaSun className="mx-3 text-gray-600 hover:text-gray-200 cursor-pointer" onClick={themeHandler} /> : <FaMoon className="mx-3 text-gray-600 hover:text-gray-200 cursor-pointer" onClick={themeHandler} />}
        </div>
      </div>
    </header>
  )
}

export default Header
